import { useState, useEffect } from 'react';
import BaseLayout from '../../layouts/BaseLayout/BaseLayout.jsx'

const Demo = () => {
	const [count1, setCount1] = useState(0);
	const [count2, setCount2] = useState(111);
	//默认的行为，会每次 render 后都执行，一般表单控制中使用
	useEffect(() => {
		console.log('useEffect 没有参数')
	});
	//传空数组，空数组跟空数组是相同的，所以useEffect只会执行一次
	useEffect(() => {
		console.log('useEffect 参数为:[]')
	}, []);
	//传空数组，空数组跟空数组是相同的，所以useEffect只会执行一次
	useEffect(() => {
		console.log('useEffect 参数为:[count1]', count1)
	}, [count1]);
	//传空数组，空数组跟空数组是相同的，所以useEffect只会执行一次
	useEffect(() => {
		console.log('useEffect 参数为:[count2]', count2)
	}, [count2]);
	//返回时传递一个函数进行卸载，在组件卸载时候调用
	useEffect(() => {
		return () => {
			console.log('useEffect 函数内部返回一个函数'); 
		}
	});
	return (
		<BaseLayout>
			<h3>hooks 的 useEffect 周期</h3>
			<button onClick={() => setCount1(count1 + 1)}>变量:count1:{count1}</button>
			<button onClick={() => setCount2(count2 + 1)}>变量:count2:{count2}</button>
		</BaseLayout>
	);
};

export default Demo;
